import Vue from 'vue';
import Component from 'vue-class-component';
import { Action } from 'vuex-class';
import { Watch } from 'vue-property-decorator';

// import Utils from '~/utils';
import { CACHE_NAME } from '~/cache';
import { ToastMsg } from '~/entity/toast';
import Api from '~/api';

import bus from '~/bus/index';

@Component({})
export default class AddCompanys extends Vue {

    companyTemp: string = null;
    companySuggestList = [];
    companyList: string[] = [];
    maxCount: number = 5;

    searchEnable = true; // 避免重复搜索

    @Action('showToast')
    showToast;

    get companyCache() {
        return bus.companyCache; 
    }
    // get companyTemp() {
    //     if (this.companyTemp.length === 0 || this.companyTemp === null) {
    //         return '';
    //     } else {
    //         // 搜索公司
    //         this.searchCompany(this.companyTemp);
    //     }
    // }

    // set companyTemp(value: string) {
    //     if (value.length === 0) {
    //         return;
    //     } else {
    //         // 搜索公司
    //         this.searchCompany(value);
    //     }
    // }

    @Watch('companyTemp')
    watchInput(newVal, oldVal) {
        if (newVal.length === 0 || newVal === null) {
            this.companySuggestList = [];
            return;
        } else {
            // 搜索公司
            this.searchCompany(this.companyTemp);
        }
    }

    async searchCompany(value: string) {
        if (value.length > 0 && this.searchEnable) { 
            this.searchEnable = false;
            const res = await Api.searchCompany(value);
            const companys = res.body.obj;
            const companyRes = companys;
            this.companySuggestList = companyRes;
            this.isCompanySelected(this.companySuggestList); // 判断请求返回的是否已经包含在选中的里面
            this.searchEnable = true;
        }
    }

    isCompanySelected(companys) {
        const selectedCompanys = this.companyList;
        companys.map(company => {
            company.selected = false;
            for (const selectedCompany of selectedCompanys) {
                if (selectedCompany === company.coname) {
                    company.selected = true;
                    break;
                }
            }
            return company;
        });
    }

    toggleCompanyFromSuggest(company) {
        if (!company.selected) { // 没选中 -> 选中
            if (this.companyList.length >= this.maxCount) {
                this.showToast(new ToastMsg('最多选择5个单位'));
                return;
            }
            this.companyList.push(company.coname);
            company.selected = !company.selected;
            // this.companyTemp = '';
        } else { // 选中 -> 没选中
            const index = this.companyList.indexOf(company.coname);
            this.companyList.splice(index, 1);
            company.selected = !company.selected;
        }
        
    }

    deleteCompany(company) {
        console.log(company);
        const index = this.companyList.indexOf(company);
        this.companyList.splice(index, 1);
        for (const companyItem of this.companySuggestList) {
            if (companyItem.coname === company) {
                companyItem.selected = !companyItem.selected;
                break;
            }
        }
    }

    activated() {
        // this.companyList = Utils.getItem(CACHE_NAME.addCompanyInOpenTask, false);
        this.companyList = this.companyCache;
    }

    deactivated() {
        // Utils.saveItem(CACHE_NAME.addCompanyInOpenTask, this.companyList);
        // bus.$emit(CACHE_NAME.addCompanyInOpenTask, this.companyList);
        this.companySuggestList = [];
        this.companyTemp = '';
    }

}



